@import url(https://fonts.googleapis.com/css?family=Bree+Serif&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  background-image: url('https://ak8.picdn.net/shutterstock/videos/24263918/thumb/1.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
}

.header {
  display: flex;
  justify-content: center;
  font-family: 'Bree Serif', serif;
  background-color: rgba(255,10,10,.7);
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 80vh;
  font-family: 'Bree Serif', serif;
  font-size: 30px;
  color: white;
  background-color: rgba(0,0,0,.5);
  margin: 5%;
  margin-top: 0;

}

.formdiv {

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 40vh;
  width: 50%;
}

button {
  background-color: grey;
  margin-bottom: 30%;
  width: inherit;
}

input {
  text-align: center;
}

.optionsContainer {
  display: flex;
  flex-direction: column-reverse;
}

.optionDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(0,0,0,.5);
  color: white;
  margin: 5%;
  height: 150px;
}

.optionsDiv button {

  width: 50px;
}

.selectedDiv {
  font-family: 'Bree Serif', serif;
  background-color: rgba(0,0,0,1);
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  margin: 5%;
}

h1 {
  font-family: 'Bree Serif', serif;
  background-color: rgba(0,0,0,.5);
  color: white;
  margin: 5%;
}

.finalContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  margin: 5%;
  height: 80vh;
}

.finalContainer h1 {
  width: 100%;
}

.finalDiv {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Bree Serif', serif;
  background-color: rgba(0,0,0,.5);
  color: white;
  margin: 5%;
  height: 30vh;
  width: 80%;
}
/*tablet*/
@media (min-width: 768px) {

  .optionsContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .optionsDiv {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    margin: 0;
  }

  .optionDiv {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* align-items: center; */
    background-color: rgba(0,0,0,.5);
    color: white;
    margin: 2%;
    width: 20%;
    height: 150px;
  }

  .optionDiv h3 {
    margin-bottom: 0;
  }

  .optionDiv button {
    margin-bottom: 10px;
  }
  .selectedDiv {
    display: flex;
    justify-content: space-between;
    font-family: 'Bree Serif', serif;
    background-color: rgba(0,0,0,.5);
    color: white;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    margin: 5%;
    margin-top: 0;
  }

  .selectedDiv h1 {
    width: 49%;
  }

  .selectedDiv div {
    width: 49%;
    margin: 0 auto;
  }

  .selected {
    border: 3px solid red;
  }

}

